/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; margin-left: auto; margin-right: auto; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; position: relative; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-arrow.slick-disabled { display: none; }

.slick-arrow { padding: 0; font-size: 12px; z-index: 30; cursor: pointer; width: 60px; height: 25px; border: none; background: transparent; box-shadow: none; color: #0F0F0F; }

.slick-arrow:hover { color: #034EA2; }

.slick-disabled { visibility: hidden; opacity: 0; }

.slick-prev { left: 0px; }

.slick-next { right: 0px; }

.slick-dots { text-align: center; font-size: 0; margin: 0 20px; display: flex; align-items: center; }

.slick-dots li { display: inline-block; list-style: none; margin: 0 3px; cursor: pointer; }

.slick-dots li button { border: 1px solid rgba(40, 40, 40, 0); display: flex; align-items: center; justify-content: center; padding: 0; font-size: 0; width: 25px; height: 25px; border-radius: 50%; overflow: hidden; background: transparent; }

.slick-dots li button:before { content: ''; display: block; border-radius: 50%; background: #0F0F0F; opacity: 0.5; width: 5px; height: 5px; }

.slick-dots li button:hover:before { background-color: #0F0F0F; width: 9px; height: 9px; }

.slick-dots li:hover button { border: 1px solid rgba(40, 40, 40, 0.5); }

.slick-dots li:hover button:before { opacity: 1; background-color: #ED1A3B; width: 9px; height: 9px; }

.slick-dots .slick-active button { border: 1px solid rgba(40, 40, 40, 0.5); }

.slick-dots .slick-active button:before { opacity: 1; background-color: #ED1A3B; width: 9px; height: 9px; }
